body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #282c34;
  color: white;
  height: 100vh;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 50px;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  min-height: 100px;
  padding: 30px 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.list-item {
  padding: 10px 15px;
  cursor: pointer;
  transition: color 0.5s;
}
.list-item:hover {
  color: #61dafb;
}

.live-active {
  color: #61dafb;
}

.width-500 {
  width: 500px;
}
.width-600 {
  width: 600px;
}
.width-700 {
  width: 700px;
}

.margin-bottom-20 {
  margin-bottom: 20px;
}
.margin-bottom-30 {
  margin-bottom: 30px;
}
.margin-top-20 {
  margin-top: 20px;
}

.flex {
  display: -webkit-flex;
  display: flex;
}
.inline-flex {
  display: -webkit-inline-flex;
  display: inline-flex;
}
.flex-column {
  -webkit-flex-direction: column;
          flex-direction: column;
}
.flex-row {
  -webkit-flex-direction: row;
          flex-direction: row;
}
.flex-wrap {
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.justify-center {
  -webkit-justify-content: center;
          justify-content: center;
}
.align-center {
  -webkit-align-items: center;
          align-items: center;
}

.font-20 {
  font-size: 20px;
}

.none {
  display: none;
}

.col-61dafb {
  color: #61dafb;
}

.word-wrap {
  white-space: normal;
  word-break: break-all;
  word-wrap: break-word;
}

.list-li {
  margin-left: 30px;
}

.input-textarea {
  width: 500px;
  height: 200px;
  background-color: #282c34;
  color: white;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  border-radius: 3px;
  resize: none;
  padding: 10px;
}

.input-textarea:focus-visible {
  outline: none;
  border: 1px solid #61dafb;
}

.input-output {
  width: 500px;
  height: 200px;
  word-break: break-all;
  overflow: auto;
  border-radius: 3px;
  border: 1px solid #767676;
  padding: 10px;
  font-size: 10px;
  color: #61dafb;
}

#author {
  margin-top: 30px;
  text-align: center;
  font-weight: 800;
  color: #b9b9b9;
  font-size: 18px;
  -webkit-animation: shadowText 10s infinite;
          animation: shadowText 10s infinite;
}

@-webkit-keyframes shadowText {
  0% {
  }
  20% {
    text-shadow: -5px 4px 3px rgba(255, 255, 255, 0.5);
    color: rgb(255, 255, 255);
  }
  50% {
    text-shadow: 0px 4px 3px rgba(97, 218, 251, 0.5);
    color: rgb(97, 218, 251);
  }
  75% {
    text-shadow: 5px 4px 3px rgba(64, 160, 255, 0.5);
    color: rgb(64, 160, 255);
  }
  100% {
  }
}

@keyframes shadowText {
  0% {
  }
  20% {
    text-shadow: -5px 4px 3px rgba(255, 255, 255, 0.5);
    color: rgb(255, 255, 255);
  }
  50% {
    text-shadow: 0px 4px 3px rgba(97, 218, 251, 0.5);
    color: rgb(97, 218, 251);
  }
  75% {
    text-shadow: 5px 4px 3px rgba(64, 160, 255, 0.5);
    color: rgb(64, 160, 255);
  }
  100% {
  }
}

@media (max-width: 18.75rem) {
  #author {
    font-size: 0.75rem;
  }
}

